.App-main {
  min-height: 100vh;
  width: 100%;
}

.App-task {
  max-width: 700px;
  width: 100%;
  min-height: 400px;
  box-shadow: 0 2px 6px #555;
  margin: 50px auto;
  padding: 50px;
  position: relative;
  background: #870000; 
  background: -webkit-linear-gradient(to right, #190A05, #870000);  
  background: linear-gradient(to top, #870000, #EF629F); 
}

.App-thought {
  max-width: 1000px;
  margin: 30px auto;
  background: #f7ff00;  
  background: -webkit-linear-gradient(to right, #db36a4, #f7ff00);  
  background: linear-gradient(to top, #db36a4, #f7ff00); 
}

.Message, .List {
  margin-top: 30px;
}

h1 {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 900;
  color: #666;
}

form {
  width: 100%;
}

form input {
  flex: 3;
  width: 100%;
  border: none;
  outline: none;
  border-bottom: 1px solid #555;
  margin: 0 5px;
}

textarea {
  width: 80%;
}

form button{
  flex: 1;
  background: #555;
  color: white;
  letter-spacing: 2px;
  cursor: pointer;
}

ul li{
  list-style: none;
  border: 1px solid #ccc;
  margin: 10px 0;
  margin-left: -35px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: capitalize;
}

ul li .active{ 
  opacity: 0.5;
  text-decoration: line-through;
}

ul li input, .row_ input {
  margin-right: 5px;
  width:max-content;
  transform: translateY(1px);
}
ul li button, .row_ button {
  border: none;
  outline: none;
  color: white;
  background: orange;
  padding: 5px 15px;
  cursor: pointer;
}
ul li button:disabled{
  opacity: 0.5;
  cursor: not-allowed;
}
.row_ {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.row_ #delete {
  background: crimson;
}
.row_ p {
  transform: translateY(8px);
}
label {
  cursor: pointer;
}
@media (max-width:400px){
  .App{
    padding: 10px
  }
}
.glass {
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2); 
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
}  

